import './Login.scss'
import $ from 'jquery'
import { auth } from './resources/Firebase';
import { useForm } from "react-hook-form";
import { useHistory } from 'react-router-dom';

const Login = () => {
    let history = useHistory();
    const {handleSubmit} = useForm({});

    function loguearse() {
        var login = {}
        login.email = $('#login-email').val()
        login.password = $('#login-password').val()
        auth.signInWithEmailAndPassword(login.email, login.password)
        .then((user) => {
            history.push('admin')
        })
        .catch((error) => {
            history.push('login')
        });
    }

    return (
        <>
            <h4 className="text-light titulo-h4 mt-4">Login</h4>
            <form id="form-login" onSubmit={e => e.preventDefault()}>
                <input className="form-control form-control-sm mt-2" id="login-email" type="email" name="email" placeholder="Email" required="required" />
                <input className="form-control form-control-sm mt-2" id="login-password" type="password" name="password" placeholder="Contraseña" required="required" />
                    <div className="d-flex">
                        <button onClick={handleSubmit(loguearse)} className="btn btn-1 btn-sm mt-2 mr-2" id="login-submit">Entrar</button>
                        <button onClick={() => history.push('resetpassword')} className="btn btn-1 btn-sm mt-2" id="olvido">He olvidado la contraseña</button>
                        <button onClick={() => history.push('/')} className="btn btn-2 btn-sm mt-2 ml-2">Volver</button>
                    </div>
            </form>
        </>
    )
}

export default Login;